#experience{
  padding: 30px 0;

}

.experience-list{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 40px;
  margin-top: 50px;
}

.experience-list div {
  background: #262626;
  padding: 40px;
  font-size: 13px;
  font-weight: 300;
  border-radius: 10px;
  transition: background 0.5s, transform 0.5s;
}

.experience-list div i{
  font-size: 50px;
  margin-bottom: 30px;
}

.experience-list div h2{
  font-size:  30px;
  font-weight: 500;
  margin-bottom: 15px;
  color: var(--gold-color);
}

.experience-list div a{
  text-decoration: none;
  color: #fff;
  font-size: 12px;
  margin-top: 20px;
  display: inline-block;
}

.experience-list div:hover{
  background:#ff004f;
  transform: translateY(-10px);

}
.experience-list ul li p{
  font-size: 1rem;
  padding-bottom: 5px;
}

#progressBar{
  width: 90%; 
  height: 15px;
  background-color: #000000;
  border-radius: 5px;
  border: none;
  
}
